@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
html, body, #root {
    height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App_Layout__4oUir {
    display: grid;
    grid-template-columns: 1fr minmax(0px, 600px) 1fr;
    grid-template-rows: repeat(3, auto);
    font-family: 'Varela Round', sans-serif;
    height: 100%;
    padding: 0px 5px;
    color: #333;
}
.App_ContentColumn__G7_hF {
    grid-column: 2 / 3;
}
.App_Title__2qxNr {
    display: flex;
    justify-content: center;
    font-size: 3rem;
}
.App_Paragraph__1O3cU {
    font-size: 1.1rem;
    text-align: justify;
}
.App_ContentCenterRow__3kkXI {
    display: flex;
    justify-content: space-around;
    margin: 50px 0px;
}
.App_Generator__RPLKv {
    height: 100px;
    width: 100px;
    border: 4px dashed #DDD;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: lightsalmon;
}
.App_Button__3SFOE {
    height: 50px;
    width: 200px;
    border: 4px solid lightsalmon;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background: transparent;
    transition: background 0.2s;
    font-size: 1.1rem;
}
.App_Button__3SFOE:hover {
    background: lightsalmon;
}
.App_Button__3SFOE[disabled] {
    border: 4px solid #DDD;
    background: #DDD;
    cursor: not-allowed;
}

